import React from 'react';
import { motion } from 'framer-motion';
import '../styles/Footer.css';

const footerLinks = [
  {
    title: '',
    links: ['', '', '', '', '']
  },
  {
    title: '',
    links: ['', '', '', '', '']
  },
  {
    title: '',
    links: ['', '', '', '', 'API']
  },
  {
    title: '',
    links: ['', '', '', '']
  }
];

const Footer = () => {
  return (
    <footer className="footer">
      <div className="container">
        <div className="footer-top">
          <motion.div 
            className="footer-brand"
            initial={{ opacity: 0, y: 20 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true }}
            transition={{ duration: 0.5 }}
          >
            <h2 className="footer-logo">Web</h2>
            <p className="footer-tagline"></p>
            <div className="social-links">
              {['', '', 'GitHub', 'Dribbble'].map((platform, index) => (
                <motion.a 
                  href={`#${platform}`} 
                  key={platform}
                  whileHover={{ y: -5 }}
                  whileTap={{ scale: 0.95 }}
                >
                  {platform}
                </motion.a>
              ))}
            </div>
          </motion.div>

          <div className="footer-links">
            {footerLinks.map((column, colIndex) => (
              <motion.div 
                className="footer-column" 
                key={column.title}
                initial={{ opacity: 0, y: 20 }}
                whileInView={{ opacity: 1, y: 0 }}
                viewport={{ once: true }}
                transition={{ duration: 0.5, delay: 0.1 * (colIndex + 1) }}
              >
                <h3 className="footer-heading">{column.title}</h3>
                <ul>
                  {column.links.map((link, linkIndex) => (
                    <motion.li 
                      key={link}
                      whileHover={{ x: 5 }}
                      transition={{ type: "spring", stiffness: 400 }}
                    >
                      <a href={`#${link}`}>{link}</a>
                    </motion.li>
                  ))}
                </ul>
              </motion.div>
            ))}
          </div>
        </div>

        <motion.div 
          className="footer-bottom"
          initial={{ opacity: 0 }}
          whileInView={{ opacity: 1 }}
          viewport={{ once: true }}
          transition={{ duration: 0.5, delay: 0.6 }}
        >
          <p>&copy; {new Date().getFullYear()} Web. .</p>
          <p>  </p>
        </motion.div>
      </div>
    </footer>
  );
};

export default Footer; 